.widgets {
    flex: 0.3;
  }
  
.widgets__input {
  display: flex;
  align-items: center;
  background-color: var(--twitter-background);
  padding: 10px;
  border-radius: 20px;
  margin-top: 10px;
  margin-left: 20px;
}

.widgets__input > input {
  border: none;
  background-color: var(--twitter-background);
}

.widgets__searchIcon {
  color: gray;
}

.widgets__widgetContainer {
  margin-top: 15px;
  margin-left: 20px;
  padding: 20px;
  background-color: #f5f8fa;
  border-radius: 20px;
}

.widgets__widgetContainer > h2 {
  font-size: 18px;
  font-weight: 800;
}

.footer a {
  color: gray;
  justify-content: center;
  text-decoration: none;
}

.footer-links {
  text-align: center;
}

.top-links {
  margin-bottom: 5px;
}

.bottom-links {
  margin-top: 5px;
}

.footer-info{
  margin-top: 10px;
  justify-content: center;
  color: gray;
  text-align: center;
}